import "mvcomponents/assets/mvtheme.min.css!";
import "mvcomponents/assets/mvcomponents.css!";
import "./globals";

/** 
 * @memberof module:mvcomponents/core
 */
export class SystemApplicationStatic {
	private _uid:number;
	private _projectVersion:string;
	public relativePathPackage: string;
	constructor(){
		this._uid = 571;
		this._projectVersion = "1";
		this.relativePathPackage = "";
	}
	public getUid(): number {
		this._uid++;
		return this._uid;
	}
	public getLastUid(): number {
		return this._uid;
	}
	public getNextUid(): number {
		return this._uid + 1;
	}
	/**
	 * recupera um parametro de url pelo nome
	 * @param p_name {string}
	 * @example
	 * SystemApplication.getUrlParam("my_url_param_var");
	 * //return 0212454
	 * @returns {string}
	 */
	public getUrlParam(p_name:string):string {
		var results = new RegExp(`[\?&]${p_name}=([^&#]*)`).exec(window.location.href);
		if (results === null) {
			return "";
		}
		return results[1] || "";
	}
	private removeInvalidChars(p_url:string):string{
		var indexOfSharp:number = p_url.indexOf("#");
		if(indexOfSharp > -1){
			p_url = p_url.substring(0,indexOfSharp);
		}
		var indexOfCharQuestion:number = p_url.indexOf("?");
		if(indexOfCharQuestion > -1){
			p_url = p_url.substring(0,indexOfCharQuestion);
		} 
		return p_url;
	}
	public getLocation(): string {
		var tmpHref:string = window.location.href;
		tmpHref = this.removeInvalidChars(tmpHref);
		return tmpHref.substring(0,tmpHref.lastIndexOf("/")+1);
	}
	public getDomain():string{
		let protocolL = window
				.document
				.location
				.protocol;

		let hostL = window
						.document
						.location
						.host;	

		return `${protocolL}//${hostL}`;
	}
}

export var SystemApplication: SystemApplicationStatic = new SystemApplicationStatic();
